.newsitembox {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin: 0.5rem 0;
  color: white;
}
.news_image {
  width: 20rem;
  height: 10rem;
  margin-right: 1rem;
  transition: all .4s;
}
.item_title {
  font-size: 1rem;
}
.item_description {
  font-size: 1rem;
}
.item_btn {
  width: 17rem;
  height: 5rem;
  background-color: #007fff;
  margin-right: 1rem;
}
.item_link {
  color: #007fff;
}
.item_info {
  margin: 0 7rem;
}
.box_url {
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #e1ebee;
  height: 10rem;
  overflow: hidden;
  color: rgb(49, 47, 47);
  text-decoration: none;
  border: 3px solid black;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.45) 0px 25px 20px -20px;
  transition: all .3s;
}
.box_url:hover{
  color: rgb(99, 99, 99);
}
.box_url:hover .news_image{
  transform: scale(1.1);
}