.navbar{
    width: 100%;
    height: 5rem;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
    position: sticky;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 100;
}
.name{
    font-size: 1.5rem;
    color: azure;
    text-decoration: underline;
    margin-left: 2rem;
}
.navbar_categories{
    display: flex;
    align-items: center;
}
.newclass {
    border: 2px solid rgb(85, 81, 81);
    border-radius:  10px;
    margin: 0 5px;
    color: aqua; 
}
.newclass:hover{
    color:rgb(113, 180, 192)
}